import React, { useEffect, useCallback, useState, useContext } from 'react'
import { useHistory } from 'react-router-dom'
import styled from 'styled-components'

import { ModalContext } from '../containers/ModalContainer'

import ScoreBox from '../components/ScoreBox'
import VolumeSlider from '../components/VolumeSlider'
import GameTable from '../components/GameTable'
import Timer from '../components/Timer'

import jelly1 from '../assets/images/jelly_type_1.png'
import jelly2 from '../assets/images/jelly_type_2.png'
import jelly3 from '../assets/images/jelly_type_3.png'
import jelly4 from '../assets/images/jelly_type_4.png'
import jelly5 from '../assets/images/jelly_type_5.png'
import jelly6 from '../assets/images/jelly_type_6.png'
import icon_home from '../assets/images/home.png'
import icon_replay from '../assets/images/replay.png'

import effect_btn from '../assets/media/effect_buttonclick.mp3'
import effect_mouse from '../assets/media/effect_mouseup.mp3'
import music_background from '../assets/media/music_background.mp3'

const GameLayout = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  padding: 12px;
`;

const GameLayoutInner = styled.div`
  width: 895px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

const Toolbar = styled.div`
  height: 35px;
  line-height: 35px;
  padding-right: 56px;

  .left {
    float: left;
    text-align: left;
  }

  .right {
    float: right;
    text-align: right;

    >:not(:last-child) {
      margin-right: 5px;
    }
  }

  .left, .right {
    > div {
      display: inline-block;
    }
  }

  * {
    font-size: 20px;
    font-weight: bold;
    color: #66a7ba;
  } 

  > * {
    margin-bottom: 10px;
  }
`;

const ImgButton = styled.button`
  position: relative;
  width: 100px;
  height: 35px;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  text-align: left;

  img {
    width: 30px;
    height: 30px;
    position: absolute;
    transform: translate(0, -3px);
  }

  &:hover {
    font-size: 22px;
  }
`;

const HowToButton = styled.p`
  position: absolute;
  width: 25px;
  height: 25px;
  margin: 0 !important;
  border: 3px solid #66a7ba;
  border-radius: 50%;
  color: #66a7ba;
  font-size: 15px;
  font-weight: bold;
  text-align: center; 
`

const jellyList = [jelly1, jelly2, jelly3, jelly4, jelly5, jelly6];

const DEFAULT_VOLUME = 0.3;
const effectMouseAudio = new Audio(effect_mouse);
const effectBtnAudio = new Audio(effect_btn);
const musicAudio = new Audio(music_background);

musicAudio.loop = true;
musicAudio.volume = DEFAULT_VOLUME;
effectBtnAudio.volume = DEFAULT_VOLUME * 1.5;
effectMouseAudio.volume = DEFAULT_VOLUME * 1.5;

// Í∏∞Îä• : min Ïù¥ÏÉÅ max ÎØ∏ÎßåÏùò ÎûúÎç§Í∞íÏùÑ Î∞òÌôò
// Ïù∏Ïûê : ÏµúÏÜüÍ∞í min, ÏµúÎåìÍ∞í max
const getRandomInt = (min, max) => {
  return Math.floor(Math.random() * (max - min)) + min;
}

// Í∏∞Îä• : Î∞∞Í≤ΩÏùåÏïÖ reset
// Ïù∏Ïûê : ÏóÜÏùå
const resetMusic = () => {
  musicAudio.pause();
  musicAudio.playbackRate = 1.0;
  musicAudio.currentTime = 0;
}

// Í∏∞Îä• : Î∞∞Í≤ΩÏùåÏïÖ ÏÜçÎèÑ Ï°∞Ï†à
// Ïù∏Ïûê : ÏÜçÎèÑ Í∞í speed (Í∏∞Î≥∏ 1.0)
const changeMusicRate = (speed) => {
  musicAudio.playbackRate = speed;
}

// Í∏∞Îä• : Î™®Îì† Audio Í∞ùÏ≤¥ Î≥ºÎ•® Ï°∞Ï†à
// Ïù∏Ïûê : Î≥ºÎ•® Í∞í volume (Í∏∞Î≥∏ 1.0)
const changeVolume = (volume) => {
  // effectVolumeÏù¥ Î∞∞Í≤ΩÏùåÏïÖÎ≥¥Îã§ ÏûëÍ∏∞ ÎïåÎ¨∏Ïóê 1.5Î∞∞
  const effectVolume = volume * 1.5;
  effectBtnAudio.volume = effectVolume > 1 ? 1 : effectVolume;
  effectMouseAudio.volume = effectVolume > 1 ? 1 : effectVolume;

  musicAudio.volume = volume;
}

export default function GamePage() {
  const [list, setList] = useState([]); // 2Ï∞®Ïõê Î∞∞Ïó¥ Array
  const [score, setScore] = useState(0); // Ï†êÏàò number
  const [isGameOver, setIsGameOver] = useState(false); // Í≤åÏûÑÏò§Î≤Ñ Ïó¨Î∂Ä boolean
  const [playCnt, setPlayCnt] = useState(0); // Í≤åÏûÑ Ïû¨ÏãúÏûë Ïπ¥Ïö¥Ìä∏ number
  const modalContext = useContext(ModalContext);
  let history = useHistory();

  // Í∏∞Îä• : 2Ï∞®Ïõê Î∞∞Ïó¥ Array ÏÉùÏÑ±
  // Ïù∏Ïûê : ÏóÜÏùå
  const createList = useCallback(() => {
    // ÏÑ†Ïñ∏Ìòï üëâüèª
    const temp = Array.from(Array(10), () => Array(15).fill(null).map(() => {
      return {
        visible: true,
        value: getRandomInt(1, 10),
        src: jellyList[getRandomInt(0, jellyList.length)],
      }
    }))

    /* Î™ÖÎ†πÌòï üëâüèª
    for (let i = 0; i < 10; i++) {
      temp.push([]);

      for (let j = 0; j < 15; j++) {
        temp[i].push({
          visible: true,
          value: getRandomInt(1, 10),
          src: jellyList[getRandomInt(0, jellyList.length)],
        })
      }
    } */

    setList(temp);
  }, [])

  // Í∏∞Îä• : '/' urlÎ°ú Ïù¥Îèô
  // Ïù∏Ïûê : ÏóÜÏùå
  const handleHomeButton = useCallback(() => {
    history.push('/');
  }, [history])
  
  // Í∏∞Îä• : playCnt state Í∞í Ï¶ùÍ∞Ä
  // Ïù∏Ïûê : ÏóÜÏùå
  const handleReplayButton = useCallback(() => {
    setPlayCnt(v => v + 1);
  }, [])

  // Ïó≠Ìï† : playCnt state Î≥ÄÍ≤ΩÏãú replay Ï≤òÎ¶¨
  useEffect(() => {
    musicAudio.play();
    setIsGameOver(false);
    setScore(0);
    createList();
  }, [playCnt, createList])
  
  // Ïó≠Ìï† : GamePage.jsx mountÏãú audio Î≥ºÎ•® defaultÎ°ú ÏÑ§Ï†ï
  useEffect(() => {
    changeVolume(DEFAULT_VOLUME);
  }, [])

  return (
    <GameLayout>
      <HowToButton className="pointer"
        onClick={() => { modalContext.modal.current.toggle() }}>
        ?
      </HowToButton>
      <GameLayoutInner>
        <Toolbar>
          <div className="left">
            <ImgButton onClick={() => {
              handleHomeButton();
              effectBtnAudio.play();
            }}>
              Home<img alt="home" src={icon_home} />
            </ImgButton>
            <ImgButton onClick={() => {
              handleReplayButton();
              effectBtnAudio.play();
            }}>
              Replay<img alt="replay" src={icon_replay} />
            </ImgButton>
          </div>
          <div className="right">
            <VolumeSlider changeVolume={changeVolume} defaultVolume={DEFAULT_VOLUME} />
            <ScoreBox score={score} />
          </div>
        </Toolbar>
        <GameTable
          list={list}
          isGameOver={isGameOver}
          changeList={setList}
          changeScore={setScore}
          audio={effectMouseAudio}
        >
          <Timer playCnt={playCnt} changeIsGameOver={setIsGameOver}
            resetMusic={resetMusic} changeMusicRate={changeMusicRate} />
        </GameTable>
      </GameLayoutInner>
    </GameLayout>
  )
}
