import React, { useEffect, useState, useCallback, useRef, } from 'react'
import styled from 'styled-components';

import GameOverBox from './GameOverBox';
import DragComponent from './DragComponent';
import particle from '../assets/images/particle.gif'

const Wrapper = styled.div`
  min-height: 600px;

  > * {
    display: inline-block;
  }

  > div {
    position: relative;
  }

  .no-drag {
    -ms-user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    user-select: none;
    -webkit-user-drag: none;
  }
`;

const Table = styled.table`
  width: 838px;
  height: 608px;
  background: #171a1e;
  border: 3px solid #66a7ba;
  border-radius: 10px;
  padding: 10px;

  td {
    position: relative;
  }
`;

const ParticleImg = styled.img`
  width: 52px;
  height: 56px;
  position: absolute;
  top: 0;
  left: 0;
`

const JellyImg = styled.img`
  width: 50px;
  height: 50px;
`;

const JellyNumber = styled.p`
  background: white;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  text-align: center;
  font-weight: bold;
  color: #616161;
`;


// Particle Img Ïª¥Ìè¨ÎÑåÌä∏
const Particle = (props) => {
  const { r, c } = props

  useEffect(() => {
    setTimeout(() => {
      const el = document.getElementById('particle-' + r + '-' + c);
      if (el) el.style.visibility = 'hidden';
    }, 500)
  }, [r, c])

  return <ParticleImg alt="particle" id={'particle-' + r + '-' + c} src={particle} />
};

// Table ÎÇ¥ tr, td ÌÉúÍ∑∏ Ïª¥Ìè¨ÎÑåÌä∏
const BearList = (props) => {
  const { list, mouseEvent, checkBear, } = props;

  return list.map((row, idxr) => {
    // idxr : idx + row
    // idxc : idx + col
    return <tr key={"row-" + idxr}>{
      row.map((col, idxc) => {
        return <td key={"col-" + idxc}
          onMouseDown={(e) => {
            if (!col.visible) return;
            mouseEvent(e, true)
            checkBear(idxr, idxc, "Down");
          }}
        >
          {!col.visible && <Particle r={idxr} c={idxc} />}
          <JellyImg
            style={{ 'opacity': col.visible ? 1 : 0 }}
            alt="jelly" className={`no-drag detection ${col.visible && 'pointer'}`}
            key={"jelly-" + idxc} src={col.src} />
          <JellyNumber
            style={{ 'opacity': col.visible ? 1 : 0 }}
            className={`detection ${col.visible && 'pointer'}`}>
            {col.value}
          </JellyNumber>
        </td>
      })
    }</tr>;
  })
}

// Í∏∞Îä• : ÎìúÎûòÍ∑∏ ÏòÅÏó≠ ÏÇ¨Ïù¥Ï¶à Í≥ÑÏÇ∞
// Ïù∏Ïûê : ÏùåÏàò Í∞í Ïó¨Î∂Ä bool, ÎÑàÎπÑ ÎòêÎäî ÎÜíÏù¥ Í∞í object
const computedDragAreaSize = (isMinus, { width, height }) => {
  // border-spacing: 2px;
  const standard = width ? 54 : 58
  const value = width ? width : height

  if (isMinus) return standard * (Math.ceil(value * -1 / standard) + 1)
  else return standard * Math.ceil(value / standard)
}

// Í∏∞Îä• : ÎßàÏö∞Ïä§ Ïù¥Î≤§Ìä∏ Ïò§Î≤ÑÌÅ¥ÎùΩ Î∞©ÏßÄ
// Ïù∏Ïûê : ÎìúÎûòÍ∑∏ ÏòÅÏó≠ ÏÇ¨Ïù¥Ï¶à object, Ïã§Ìñâ Ìï®Ïàò function, ÏßÄÏó∞ ÏãúÍ∞Ñ number
const throttle = (data, fn, delay) => {
  let w = data.width
  let h = data.height
  if (!thTimer) {
    thTimer = setTimeout(() => {
      thTimer = null;
      const temp = fn();

      // ÎìúÎûòÍ∑∏ ÏòÅÏó≠ ÏÇ¨Ïù¥Ï¶àÍ∞Ä ÎßàÏßÄÎßâÏóê Í≥ÑÏÇ∞Îêú ÏÇ¨Ïù¥Ï¶àÏôÄ ÎßûÎäîÏßÄ Ï≤¥ÌÅ¨  
      if (temp.w !== w && temp.h !== h) {
        fn();
      }
    }, delay)
  }
}

let startClientX
let startClientY

let thTimer;
// let count = 0;
// let sizeCount = 0;
const GameTable = (props) => {
  const { list, children, isGameOver, changeList, changeScore, audio } = props;
  const [isDrag, setIsDrag] = useState(false);
  const [startBear, setStartBear] = useState(null);
  const dragComponentRef = useRef();

  // Í∏∞Îä• : ÏÇ¨Ïö©ÏûêÍ∞Ä ÎìúÎûòÍ∑∏ ÌïòÏßÄ ÏïäÎäî ÏÉÅÌÉú Ïùº Îïå Ï≤òÎ¶¨
  // Ïù∏Ïûê : ÏóÜÏùå
  const noDragState = useCallback(() => {
    if (isDrag) {
      setIsDrag(false);
      setStartBear(null);
      startClientX = null;
      startClientY = null;
    }
  }, [isDrag])

  // Í∏∞Îä• : Mouse Ïù¥Î≤§Ìä∏ Î∞úÏÉù Ïãú Í≤åÏûÑ Ï†êÏàò Í¥ÄÎ†® Ï≤òÎ¶¨
  // Ïù∏Ïûê : row Í∞í number, col Í∞í number, ÎßàÏö∞Ïä§ down, up ÏÉÅÌÉú Í∞í string
  const checkBear = useCallback((row, col, state) => {
    if (!startBear && state === "Down") {
      // row,colÍ∞í(2Ï∞®ÏõêÎ∞∞Ïó¥ Ïù∏Îç±Ïä§ Í∞í)ÏùÑ Î∞õÏïÑ DragComponent.jsx xPos, yPosÍ∞í Î≥ÄÍ≤ΩÌïòÍ∏∞
      setStartBear(true);
      dragComponentRef.current.setAreaPos({ x: row, y: col });
    } else if (startBear) {
      // DragComponent.jsxÏùò width, height Í∞íÏùÑ ÌÜµÌï¥ forÎ¨∏ ÎèåÎ¶¥ ÏãúÏûëÏ°∞Í±¥, Ï¢ÖÎ£åÏ°∞Í±¥ Ïû°Í∏∞ Î∞è Ï†êÏàò Í≥ÑÏÇ∞ÌïòÍ∏∞
      // border-spacing: 2px;
      const offsetX = Math.floor(dragComponentRef.current.getAreaSize().height / 58);
      const offsetY = Math.floor(dragComponentRef.current.getAreaSize().width / 54);
      const cn = dragComponentRef.current.getDirection();
      const { x, y } = dragComponentRef.current.getAreaPos();

      // start, end
      // ÎìúÎûòÍ∑∏ Î∞©Ìñ•Ïóê Îî∞Îùº Ïù¥Ï§ëforÎ¨∏ ÏãúÏûëÏ°∞Í±¥, Ï¢ÖÎ£åÏ°∞Í±¥Ïù¥ Îã¨ÎùºÏßê
      let si, ei, sj, ej;
      switch (cn) {
        case "rightBottom":
          si = x;
          ei = x + offsetX;
          sj = y;
          ej = y + offsetY;
          break;
        case "rightTop":
          si = x - offsetX + 1;
          ei = x + 1;
          sj = y;
          ej = y + offsetY;
          break;
        case "leftBottom":
          si = x;
          ei = x + offsetX;
          sj = y - offsetY + 1;
          ej = y + 1;
          break;
        case "leftTop":
          si = x - offsetX + 1;
          ei = x + 1;
          sj = y - offsetY + 1;
          ej = y + 1;
          break;
        default:
          break;
      }

      let count = 0;
      let tempArray = JSON.parse(JSON.stringify(list));
      for (let i = si; i < ei; i++) {
        for (let j = sj; j < ej; j++) {
          if (list[i][j].visible) {
            count += list[i][j].value;
            tempArray[i][j].visible = false;
          }
        }
      }

      if (count === 10) {
        changeScore(v => v + (ei - si) * (ej - sj));
        changeList(tempArray);
        audio.play();
      }

      setStartBear(null);
    }
  }, [list, startBear, changeScore, changeList, audio])

  // Í∏∞Îä• : Mouse Í¥ÄÎ†® Ïù¥Î≤§Ìä∏ Î∞úÏÉù Ïãú ÎìúÎûòÍ∑∏ Í¥ÄÎ†® Ï≤òÎ¶¨
  // Ïù∏Ïûê : ÎßàÏö∞Ïä§ Ïù¥Î≤§Ìä∏ Í∞ùÏ≤¥ object, Ïù¥Î≤§Ìä∏ Î∞úÏÉù Í∞ùÏ≤¥ <td/> Ïó¨Î∂Ä Í∞í bool
  const mouseEvent = useCallback((e, isTdTag = false) => {

    /* 
    isTdTag ÌôïÏù∏ Ïù¥Ïú† ?
    <td />Ïóê classNameÏúºÎ°ú detectionÏùÑ Ï£ºÍ≤å ÎêòÎ©¥ <tbody /> MouseMoveÏù¥Î≤§Ìä∏ Î∞úÏÉùÏãúÏóê
    ÎßàÏö∞Ïä§Í∞Ä BearÏù¥ÎØ∏ÏßÄ Ïù¥Ïô∏Ïùò ÎπàÍ≥µÍ∞ÑÏóê ÏúÑÏπòÌï¥ÎèÑ Move Ïù¥Î≤§Ìä∏Í∞Ä Ïì∏Îç∞ÏóÜÏù¥ Î∞úÏÉùÌïòÍ∏∞ ÎñÑÎ¨∏Ïóê
    detection classNameÏùÑ Ï£ºÏßÄ ÏïäÎäîÎã§. 
    
    ÌïòÏßÄÎßå <td />Ïóê Îã¨Î¶∞ MouseDown Ïù¥Î≤§Ìä∏ Î∞úÏÉùÏãú ÏúÑÏóêÏÑú ÏÑ§Î™ÖÌïú ÎπàÍ≥µÍ∞ÑÏùÑ ÌÅ¥Î¶≠Ìïú Í≤ΩÏö∞
    e.targetÏúºÎ°ú detection classNameÏù¥ Ï∂îÍ∞ÄÎêòÏßÄ ÏïäÏùÄ <td />Í∞Ä Îì§Ïñ¥Ïò§Î©¥ÏÑú
    ÏïÑÎûò Ï°∞Í±¥Î¨∏Ïóê Í±∏Î¶¨Í≤å ÎêòÍ≥† isDragÎäî false ÏÉÅÌÉúÏù¥Î©¥ÏÑú checkBear Ìï®ÏàòÍ∞Ä Ïã§ÌñâÎêòÏÑú Ïò§Î•òÍ∞Ä ÎÇòÍ≤å ÎêúÎã§.
    Îî∞ÎùºÏÑú detection className Ï°∞Í±¥ ÎòêÎäî <td /> Ïù∏ÏßÄ ÌôïÏù∏ÌïòÎäî Ï°∞Í±¥ÏùÑ Ï∂îÍ∞ÄÌï¥Ï£ºÏñ¥ Ï≤òÎ¶¨Ìï¥Ï§ÄÎã§.
    */

    if (e.target.className.includes("detection") || isTdTag) {
      if (!isDrag && e._reactName === "onMouseDown") {
        // isDrag Î≥ÄÏàò ÏÑ§Ï†ï (true), ÎßàÏö∞Ïä§ ÌÅ¥Î¶≠ÏãúÏùò ÏµúÏ¥à ÏúÑÏπò Í∞í Ï†ÄÏû•
        setIsDrag(true);

        // v === true : e.targetÏù¥ td ÌÉúÍ∑∏Ïù∏ Í≤ΩÏö∞
        startClientX = isTdTag ? e.target.getClientRects()[0].x : e.target.parentNode.getClientRects()[0].x
        startClientY = isTdTag ? e.target.getClientRects()[0].y : e.target.parentNode.getClientRects()[0].y
      }

      if (isDrag && e._reactName === "onMouseMove") {
        // ÌÅ¥Î¶≠Ïãú Ï†ÄÏû•Îêú ÏµúÏ¥à ÏúÑÏπò Í∞íÍ≥º Í≥ÑÏÜç Î∞îÎÄåÎäî ÏÇ¨Ïö©Ïûê ÏúÑÏπò Í∞í ÌååÏïÖ ÌõÑ, ÎìúÎûòÍ∑∏ ÏòÅÏó≠ Î∞©Ìñ•, ÏÇ¨Ïù¥Ï¶à ÏÑ§Ï†ï
        // count++
        // console.log('throttle Ï†ÅÏö© Ï†Ñ', count);
        throttle(dragComponentRef.current.getAreaSize(), () => {
          // sizeCount++;
          // console.log('throttle Ï†ÅÏö© ÌõÑ', sizeCount);

          const width = e.clientX - startClientX
          const height = e.clientY - startClientY

          let computedW, computedH
          if (width >= 0 && height >= 0) {
            dragComponentRef.current.setDirection("rightBottom");
            computedW = computedDragAreaSize(false, { width })
            computedH = computedDragAreaSize(false, { height })
          } else if (width < 0 && height >= 0) {
            dragComponentRef.current.setDirection("leftBottom");
            computedW = computedDragAreaSize(true, { width })
            computedH = computedDragAreaSize(false, { height })
          } else if (width >= 0 && height < 0) {
            dragComponentRef.current.setDirection("rightTop");
            computedW = computedDragAreaSize(false, { width })
            computedH = computedDragAreaSize(true, { height })
          } else {
            dragComponentRef.current.setDirection("leftTop");
            computedW = computedDragAreaSize(true, { width })
            computedH = computedDragAreaSize(true, { height })
          }


          dragComponentRef.current.setAreaSize({ w: computedW, h: computedH });

          // console.log(dragComponentRef.currnet);
          return { w: computedW, h: computedH, }
        }, 100)
      }
    }


    if (e._reactName === "onMouseUp") {
      // isDrag Î≥ÄÏàò ÏÑ§Ï†ï (false)
      noDragState();
    }
    if (e._reactName === "onMouseLeave" && e.target.tagName === "TABLE") {
      // isDrag Î≥ÄÏàò ÏÑ§Ï†ï (false)
      noDragState();
    }

  }, [isDrag, noDragState])

  useEffect(() => {
    if (isGameOver) noDragState();

  }, [isGameOver, noDragState])

  return (
    <Wrapper>
      <div className="no-drag">
        <DragComponent className="rightBottom" isDrag={isDrag} ref={dragComponentRef}
          mouseEvent={mouseEvent} checkBear={checkBear} />
        <Table onMouseLeave={mouseEvent}>
          <tbody id="tbody-area" onMouseMove={mouseEvent} onMouseUp={mouseEvent}>
            {isGameOver ?
              <GameOverBox />
              :
              <BearList list={list} mouseEvent={mouseEvent} checkBear={checkBear} />}
          </tbody>
        </Table>
      </div>
      {children}
    </Wrapper>
  )
}

export default React.memo(GameTable);
// export default GameTable;